@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --brand-primary: #4f46e5;
  --brand-secondary: #7c3aed;
  --dark-bg: #111827;
  --dark-surface: #1f2937;
  --dark-border: #374151;
  --dark-text-primary: #f9fafb;
  --dark-text-secondary: #9ca3af;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--dark-bg);
  color: var(--dark-text-primary);
}

/* Mobile-specific improvements */
@media (max-width: 640px) {
  /* Improve touch targets */
  button, .button, [role="button"] {
    min-height: 44px;
    min-width: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  /* Better spacing for mobile */
  .p-4 {
    padding: 1rem;
  }
  
  .gap-4 {
    gap: 1rem;
  }
  
  /* Ensure form inputs are easily tappable */
  input, textarea, select {
    min-height: 44px;
    padding: 0.5rem;
  }
  
  /* Improve readability on small screens */
  body {
    font-size: 16px; /* Prevent iOS zoom on input focus */
  }
  
  /* Optimize header for mobile */
  header {
    padding: 0.75rem;
  }
  
  /* Mobile header specific improvements */
  header .workspace-selector {
    width: 100%;
  }
  
  header .prompt-input {
    width: 100%;
  }
  
  /* Ensure proper spacing for mobile header elements */
  header > div {
    margin-bottom: 0.5rem;
  }
  
  header > div:last-child {
    margin-bottom: 0;
  }
  
  /* Mobile canvas controls improvements */
  .mobile-controls {
    min-width: 44px;
    min-height: 44px;
  }
  
  .mobile-controls button {
    min-width: 44px;
    min-height: 44px;
    padding: 0.75rem;
  }
  
  /* Ensure canvas controls are properly spaced on mobile */
  .canvas-control-group {
    gap: 0.5rem;
  }
  
  .canvas-control-group button {
    margin: 0.25rem;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}